.page-preview {
  position: fixed;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  z-index: 99999999;
  overflow: hidden;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  padding: 0;
  margin: 0;
  -webkit-transition: opacity 0.3s;
  transition: opacity 0.3s;
  background-color: #eee;
}
.page-preview:hover {
  cursor: zoom-out;
}
.page-preview img {
  max-height: 100%;
  max-width: 100%;
  margin: auto;
}
.page-preview img:hover {
  cursor: pointer;
}


